@import "sass/_variables.scss";@import "sass/_themes.scss";
.cq-chart-size {
    display: flex !important;
    height: auto;
    width: auto !important;
    &:after {
        display: none;
    }
    > span {
        opacity: 1;
        position: relative;
        cursor: pointer;
        transition: scale .25s;
    }
    .cq-menu-btn {
        padding: 5px 0px;
    }
    .interval-title {
        white-space: nowrap;
        width: auto;
        line-height: 25px;
        display: none; /* TODO */
    }
}
.cq-chart-control-left{
    .cq-chart-size{
        width: 100px !important;
        flex-flow: column;
    }
}


.smartcharts-desktop{
    &.smartcharts-480 {
        .cq-chart-control-bottom{
            .cq-chart-controls {
                .cq-chart-size {
                    .cq-menu-btn {
                        width: 47px;
                    }
                }
            }
        }
    }
    &.smartcharts-900 {
        .cq-chart-control-bottom{
            .cq-chart-controls {
                .cq-chart-size {
                    .cq-menu-btn {
                        width: 70px;
                    }
                }
            }
        }
    }
    &.smartcharts-1100 {
        .cq-chart-control-bottom{
            .cq-chart-controls {
                .cq-chart-size {
                    .cq-menu-btn {
                        width: 100px;
                        padding: 0px !important;
                    }
                }
            }
        }
    }
}